<template>
<div>
    <button 
        class="bp-button"
        @click="handleClick"
        @dblclick="handleDBClick"
        :disabled="buttonDisabled"
        :type="type"
        :class="[
            type ? 'button-' + type : 'button-primary',
            density ? 'button-density-' + density : 'button-density-default',
            {'btn-block': block}
        ]" 
    >
        <svg-icon 
            :iconClass="icon"
            :class="{'hide': !icon || icon=='null'}"
        ></svg-icon>
        {{text}}
    </button>

</div>
</template>

<script>
// import '../assets/css/variables.scss'
// import '../assets/css/addon.scss'
// import '../assets/css/_base.scss'
import '.././icons';
export default {
    props: {
        text: String,
        type: String, // standard, subtle, destructive, warning, 
        density: String, // comfy, default, compact
        block: Boolean, //button
        disabled: Boolean, //disableddisabled
        icon: {
            type: String,
            default: "null"
        }
    },
    computed: {
        buttonDisabled() {
            return this.disabled;
        },
        // iconHide() {
        //     if(!this.icon) {
        //         this.icon = "none";
        //         return true
        //     }
        //     return false;
        // }
    },
    methods: {
        handleClick(data) {
            var that = this
            // 300ms
            clearTimeout(that.time)
            that.time = setTimeout(() => {
                // 
                console.log("")
                this.$emit('click', data);
            },300)
        },
        handleDBClick(data) {
            clearTimeout(this.time)
            console.log("")
            this.$emit('dbclick', data)

        }
    }
}
</script>

